:root{
  /**
   * Modular Scale
   * Base: font-size 20
   * Scale: Major thrid (4:5 / 1.25)
   * Numbers: 4, 8, 13, 16, 20, 26, 32, 39, 49, 60, 75, 95, 120, 180, 232, 291, 360, 450, 570, 720
   */
  --base-font: 20px;
  --base-lineheight: 30px;
  --baseline: 10px;
  /* small */
  --size-xxs: 4px;
  --size-xs: 8px;
  --size-s: 13px;
  /* medium / default */
  --size-m: 16px;
  /* large */
  --size-l: 20px;
  --size-xl: 26px;
  --size-xxl: 32px;
  /* huge */
  --size-h: 39px;
  --size-xh: 49px;
  --size-xxh: 60px;
  /* giant */
  --size-g: 75px;
  --size-xg: 95px;
  --size-xxg: 120px;
  /* -------------------------
    Vertical Spacing
    ------------------------- */
    --vs-s: calc(var(--baseline) * 1);
    --vs-m: calc(var(--baseline) * 2);
    --vs-l: calc(var(--baseline) * 3);
    --vs-xl: calc(var(--baseline) * 5);
    --vs-xxl: calc(var(--baseline) * 8);
  /* -------------------------
    Horizontal Spacing
    ------------------------- */
    --hs-s: var(--size-s);
    --hs-m: var(--size-l);
    --hs-l: var(--size-h);
}
